HOME SECTION STYLES
============================================================ */
#home {
  text-align: center;
  padding-top: 120px;
  padding-bottom: 60px;
}

#home .social {
  display: block;
  margin: 50px 20px;
}

/*Carousel Slider*/
#carousel-slider {
  padding: 50px 20px 5px 50px;
}

#carousel-slider h3 {
  line-height: 50px;
  font-family: 'Oswald', sans-serif;
}

#carousel-slider p {
  color: #e44f4f;
  font-size: 16px;
  line-height: 25px;
  margin: 25px 20px;
}

/* =============================================================
SERVICES SECTION STYLES
============================================================ */
#services .services-wrapper {
  padding: 20px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  margin-bottom: 40px;
  background-color: rgba(255, 255, 255, 0.07);
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

#services .services-wrapper:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

#services h3 {
  font-family: 'Oswald', sans-serif;
}

#services .services-wrapper i {
  margin: 5px;
  color: #e44f4f;
  font-size: 60px;
}

/*============================
PRICING SECTION STYLES
==============================*/
.light-pricing {
  margin-bottom: 30px;
  margin-top: 50px;
  text-align: center;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
  line-height: 30px;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.light-pricing:hover {
  background-color: transparent;
}

.light-pricing ul {
  list-style: none;
  margin: 0;
  text-align: center;
  padding-left: 0px;
}

.light-pricing ul li {
  padding-top: 20px;
  padding-bottom: 20px;
  cursor: pointer;
}

.light-pricing ul li i {
  margin-right: 5px;
}

.light-pricing .price {
  padding: 40px 20px 20px 20px;
  font-size: 60px;
  font-weight: 900;
}

.light-pricing .price small {
  color: #000;
  display: block;
  font-size: 15px;
  margin-top: 22px;
}

.light-pricing .type {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 30px 20px;
  font-weight: 900;
  text-transform: uppercase;
  font-size: 30px;
  color: #e44f4f;
}

.light-pricing .pricing-footer {
  padding: 20px;
}

.db-attached > .col-lg-4,
.db-attached > .col-lg-3,
.db-attached > .col-md-4,
.db-attached > .col-md-3,
.db-attached > .col-sm-4,
.db-attached > .col-sm-3 {
  padding-left: 0;
  padding-right: 0;
}

.light-pricing.popular {
  margin-top: 10px;
}

.light-pricing.popular .price {
  padding-top: 80px;
}

/*============================
WORK SECTION STYLES
==============================*/
#work .caegories a.active {
  background-color: transparent;
  color: #fff;
}

.isotope .isotope-item {
  -webkit-transition-duration: 0.8s;
  -moz-transition-duration: 0.8s;
  transition-duration: 0.8s;
}

#work .work-wrapper {
  padding: 10px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  margin-bottom: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
